/*----------------mixins-------------------*/

.page-heading() {
  line-height: 2.14;
}

.section-heading() {
  font-weight: 300;
  font-size: 24px;
  line-height: 1.21;
  color: #283645;
}

.form-heading() {
  line-height: 1.29;
  color: #d22856;
  text-transform: uppercase;
}

.form-text() {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 1.67;
  color: #283645;
}

.list-reset() {
  margin: 0;
  padding: 0;
  list-style: none;
}

.green-button() {
  &:hover {
    background-color: #1c9b5e;
    border: 2px solid #1c9b5e;
    border-radius: 1px;
  }

  &:active {
    background-color: #1c9b5e;
    border: 2px solid #1c9b5e;
    border-radius: 1px;
    color: rgba(255, 255, 255, 0.3);
  }

  .form-text();
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #2aab6d;
  border: 2px solid #2aab6d;
  border-radius: 1px;
}
